/**
 * Debug test to understand import issues
 */

describe('SessionProfiler - Debug', () => {
  it('should debug import', () => {
    const SessionProfilerModule = require('../../src/engine/SessionProfiler');
    expect(SessionProfilerModule).toBeDefined();
    expect(Object.keys(SessionProfilerModule)).toContain('SessionProfiler');
    
    const { SessionProfiler } = require('../../src/engine/SessionProfiler');
    expect(SessionProfiler).toBeDefined();
    expect(typeof SessionProfiler).toBe('function');
    
    const profiler = new SessionProfiler();
    expect(profiler).toBeDefined();
    expect(profiler.constructor.name).toBe('SessionProfiler');
  });
});